import React, { Component } from "react";

import { api } from "../api";

let key = api.length;


const getRandom = (max, precision = 0) => {
  return (Math.random() * max).toFixed(precision);
};

const main = {
  title: ["Clouds", "Rain", "Clear"],
  description: ["light rain", "sky is clear", "broken clouds", "few clouds"]
};

export default class WeatherCreator extends Component {
  constructor(props) {
    super(props);
    this.state = {
      main: main.title[getRandom(3)],
      description: main.description[getRandom(4)],
      day: getRandom(35, 1),
      min: getRandom(20),
      max: getRandom(50),
      pressure: getRandom(1200),
      speed: getRandom(4, 3),
      clouds: getRandom(100),
      humidity: getRandom(100),
      active: false
    };
  }

  handleChange = e => {
    this.setState({
      [e.target.name]: e.target.value
    });
  };

  onSubmit = e => {
    e.preventDefault();
    const form = {
      dt: key++,
      temp: {
        day: this.state.day,
        min: this.state.min,
        max: this.state.max
      },
      pressure: this.state.pressure,
      humidity: Number(this.state.humidity),
      weather: [
        {
          main: this.state.main,
          description: this.state.description
        }
      ],
      speed: this.state.speed,
      clouds: Number(this.state.clouds)
    };
    api.push(form);
    console.log(api);
    this.clear();
  };

  random = () => {
    this.setState({
      main: main.title[getRandom(3)],
      description: main.description[getRandom(4)],
      day: getRandom(35, 1),
      min: getRandom(20),
      max: getRandom(50),
      pressure: getRandom(1200),
      speed: getRandom(4, 3),
      clouds: getRandom(100),
      humidity: getRandom(100)
    });
  };

  clear = () => {
    this.setState({
      main: "",
      description: "",
      day: "",
      min: "",
      max: "",
      pressure: "",
      speed: "",
      clouds: 0,
      humidity: 0
    });
  };

  change = () => {
    this.setState({ active: !this.state.active });
  };

  render() {
    return (
      <div
        className="card border-primary m-4 position-fixed fixed-bottom"
        style={{
          width: "18rem",
          left: "100%",
          transform: "translateX(-120%)"
        }}
      >
        <button
          className="btn btn-primary card-header bg-primary text-white"
          onClick={() => this.change()}
        >
          Weather creator
        </button>
        {this.state.active && (
          <>
            <form className="card-body">
              {Object.keys(this.state).map((key, index) => {
                if (key === "active") return null;
                return (
                  <label key={index} className="col">
                    {key[0].toUpperCase() + key.substr(1)}
                    {key === "clouds" || key === "humidity" ? (
                      <input
                        name={key}
                        min="0"
                        max="100"
                        type="range"
                        className="custom-range"
                        value={this.state[`${key}`]}
                        onChange={e => this.handleChange(e)}
                      />
                    ) : (
                        <input
                          name={key}
                          type="text"
                          className="form-control"
                          value={this.state[`${key}`]}
                          onChange={e => this.handleChange(e)}
                        />
                      )}
                  </label>
                );
              })}
              <button
                style={{ display: "none" }}
                onClick={e => this.onSubmit(e)}
              />
            </form>

            <div className="card-footer">
              <button
                className="btn btn-light btn-sm text-success col-4"
                onClick={e => this.onSubmit(e)}
              >
                Add
              </button>
              <button
                className="btn btn-light btn-sm text-primary col-4"
                onClick={() => this.random()}
              >
                Random
              </button>
              <button
                className="btn btn-light btn-sm text-danger col-4"
                onClick={() => this.clear()}
              >
                Clear
              </button>
            </div>
          </>
        )}
      </div>
    );
  }
}
