import React, { Component } from "react";

// https://getbootstrap.com/docs/4.3/components/card/
// https://undraw.co/illustrations

import Clouds from "../assets/clouds.png";
import Rain from "../assets/rain.png";
import Clear from "../assets/clear.png";
import { api } from "../api";

class ResultItem extends Component {
  constructor() {
    super();
    this.state = {
      btnMoreText: "More",
      display: false,
      progressBarValue: 0,
      Rain: Rain,
      Clouds: Clouds,
      Clear: Clear,
      active: true
    };
  }
  showDsc = () => {
    if (this.state.btnMoreText === "More") {
      this.setState({
        btnMoreText: "Less",
        display: true
      });
    } else {
      this.setState({
        btnMoreText: "More",
        display: false
      });
    }
  };

  deleteItem = dt => {
    api.map((el, i) => (el.dt === dt ? api.splice(i, 1) : null));
    console.log(api);
  };

  progressBar = value => ({ width: value + "%" });

  render() {
    const { el } = this.props;

    return (
      <div
        id={el.dt}
        className={`card m-4 ${this.state.display ? "border-primary" : null}`}
        style={{ width: "18rem" }}
      >
        <img
          src={this.state[el.weather[0].main]}
          className="card-img-top"
          alt={el.weather[0].main}
        ></img>

        <div className="card-body">
          <h5 className="card-title">{el.weather[0].main}</h5>

          <h6 className="card-subtitle mb-2 text-muted">
            {el.weather[0].description}
          </h6>

          <p className="card-text">{el.temp.day}°C</p>
        </div>
        {this.state.display && (
          <ul className="list-group list-group-flush">
            <li className="list-group-item">Min {el.temp.min}°C</li>
            <li className="list-group-item">Max {el.temp.max}°C</li>

            <li className="list-group-item">Pressure {el.pressure} hPa</li>

            <li className="list-group-item">Speed {el.speed} m/s</li>

            <li className="list-group-item">
              Clouds {el.clouds}%
              <div className="progress">
                <div
                  className="progress-bar"
                  role="progressbar"
                  style={this.progressBar(el.clouds)}
                  aria-valuemin="0"
                  aria-valuemax="100"
                ></div>
              </div>
            </li>

            <li className="list-group-item">
              Humidity {el.humidity}%
              <div className="progress">
                <div
                  className="progress-bar"
                  role="progressbar"
                  style={this.progressBar(el.humidity)}
                  aria-valuemin="0"
                  aria-valuemax="100"
                ></div>
              </div>
            </li>
          </ul>
        )}

        <div className="card-footer">
          <button
            className="btn btn-light btn-sm text-secondary col-9"
            onClick={this.showDsc}
          >
            {this.state.btnMoreText}
          </button>
          <button
            className="btn btn-light btn-sm text-danger col-3"
            onClick={() => this.deleteItem(el.dt)}
          >
            Delete
          </button>
        </div>
      </div>
    );
  }
}

export default ResultItem;
